import '../login/login.css'
import Obyav from '../../components/obyav/obyav'

import './profile.css'
import { Link, Outlet } from 'react-router-dom'

function Profile() {
    return (
        <>

            <div>
                <div style={{ marginTop: '16px' }} className='container loginInpWrapper'>
                    <div className="loginInp">
                        <select className='loginInpSel'>
                            <option className='opText'> </option>
                            <option> </option>
                            <option> </option>
                        </select>

                        <input className='loginInpSer' type={'text'} placeholder={'   ?'}></input>

                        <select className='loginInpSel2'>
                            <option className='opText' >  </option>
                            <option>2</option>
                            <option>3</option>
                        </select>

                        <button className='loginInpBtn' type={"submit"}></button>
                    </div>

                    <div>
                        <button className='leftInpBtn'>
                            <p className='leftInptext_1'>+</p>

                            <p className='leftInptext_2'> </p>
                        </button>
                    </div>
                </div>

                <div>
                    <div className="container" style={{ paddingTop: '30px' }}>
                        <div style={{ display: 'flex' }}>
                            <p className='helloYou' style={{ marginRight: '517px' }}>  </p>

                            <p className='yourSum' style={{ marginRight: '26px' }}> : 2 3000 </p>

                            <div className='sumWrapper' style={{ display: 'flex' }}>
                                <p className='sumPlus'>+</p>
                                <p className='sumText'></p>
                            </div>
                        </div>

                        <div className='profileMainMenu' style={{ display: 'flex' }}>
                            <Link style={{textDecoration: 'none'}} to=''>
                                <p className='mainMenuText'></p>
                            </Link>

                            <Link style={{textDecoration: 'none'}} to='sms'>
                                <p className='mainMenuText'></p>
                            </Link>

                            <Link style={{textDecoration: 'none'}} to='summ'>
                                <p className='mainMenuText'>  </p>
                            </Link>

                            <Link style={{textDecoration: 'none'}} to='settings'>
                                <p className='mainMenuText'></p>
                            </Link>

                            <Link style={{textDecoration: 'none'}} to='my-business'>
                                <p className='mainMenuText'> </p>
                            </Link>
                        </div>
                    </div>
                </div>

            </div>
            <Outlet />

        </>
    )
}

export default Profile