import { useFormik } from 'formik'
import { useContext, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import { AuthContext } from '../../context/authContext'
import RightImage from '../../assets/loginImg.png'
import * as Yup from 'yup'
import './login.css'
import '../../index.css'

function Login() {

    const { token } = useContext(AuthContext)

    const navigate = useNavigate()

    const { setToken } = useContext(AuthContext)

    const validationScheme = Yup.object().shape({
        name: Yup.string().required('Email kiriting'),
        password: Yup.string().min(5)
    })

    const initialValues = {
        name: '',
        password: ''
    }

    const formik = useFormik({
        initialValues,
        validationScheme,
        onSubmit: (values) => {
            console.log(values);
        }
    })

    const nameRef = useRef()
    const passwordRef = useRef()

    const handleSubmit = (e) => {

        e.preventDefault();

        fetch('https://reqres.in/api/login', {
            method: 'POST',

            headers: {
                'Content-type': 'application/json'
            },

            body: JSON.stringify({ email: nameRef.current.value, password: passwordRef.current.value })
        }).then(res => res.json())
            .then(data => {
                setToken(data.token)
            })
            .catch(err => console.log(err))
            .finally()

    }

    token ? (
        navigate('/')
    ) : (
        console.log('hello')
    )

    function handleChange() {

        if (formik.values.name == 'eve.holt@reqres.in') {
            console.log('hi')
        } else {
            alert('upss')
        }
    }



    return (
        <div className='container'>
            <div className='loginInpWrapper'>
                <div className="loginInp">
                    <select className='loginInpSel'>
                        <option className='opText'> </option>
                        <option> </option>
                        <option> </option>
                    </select>

                    <input className='loginInpSer' type={'text'} placeholder={'   ?'}></input>

                    <select className='loginInpSel2'>
                        <option className='opText' >  </option>
                        <option>2</option>
                        <option>3</option>
                    </select>

                    <button className='loginInpBtn' type={"submit"}></button>
                </div>

                <div>
                    <button className='leftInpBtn'>
                        <p className='leftInptext_1'>+</p>

                        <p className='leftInptext_2'> </p>
                    </button>
                </div>
            </div>

            <div className='mainLogin'>
                <form className='left' onSubmit={handleSubmit} onChange={formik.handleSubmit}>
                    <h3 className='leftHeading'>  !</h3>

                    <div className='leftTextWrapper'>
                        <p className='leftText'>                </p>

                        <p className='leftInpText'> email   </p>

                        <input value={formik.values.name} onChange={formik.handleChange}
                            name='name' ref={nameRef} type='email'
                            required className='leftInp'>
                        </input>

                        <p className='leftInpText'></p>

                        <input value={formik.values.password} onChange={formik.handleChange}
                            name='password' ref={passwordRef} type='password'
                            required className='leftInp'>
                        </input>

                        <button id='btn' onChange={formik.handleChange} type='submit' className='leftInp leftBtn'></button>

                        {
                            formik.errors.login && <h4 style={{ color: 'red' }}>{formik.errors.login}</h4>
                        }

                        {
                            formik.errors.password && <h4 style={{ color: 'red' }}>{formik.errors.password}</h4>
                        }


                        <div className='spanWrapper'>
                            <span className='loginSpan'></span>
                            <p className='or'></p>
                            <span className='loginSpan'></span>
                        </div>

                        <p className='createPass'>  </p>
                    </div>
                </form>

                <div className='right'>
                    <img className='rightImg' src={RightImage} alt='erkin'></img>
                </div>
            </div>
        </div>
    )
}

export default Login