import '../../index.css'
import '../home/home.css'
import '../../components/cardItems/cardItem.css'
import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

function Shows() {
    const [card, setCard] = useState({
        isFetched: false,
        data: [],
        error: null
    })

    useEffect(() => {
        fetch('https://fakestoreapi.com/products')
            .then(res => res.json())
            .then(data => {
                setCard({
                    isFetched: true,
                    data,
                    error: false
                })
            }, console.log(card))
            .catch(err => {
                setCard({
                    isFetched: false,
                    data: [],
                    error: err
                })
            })
    }, [])

    return (
        <div className="container">
            <ul  style={{display: 'flex'}}  >
                {
                    card.data.length > 0 && card.isFetched ? (
                        card.data.slice(4, 8).map(item => (
                            <li style={{ display: 'flex' }} key={item.id} className='cardItem'>
                                <Link style={{ textDecoration: 'none' }} to={`/product/${item.id}`}>
                                    <img className='cardImg' src={item.image}></img>
                                    <p className='cardTitle'>{item.title}</p>
                                    <p className='cardTime'> 22:55</p>
                                    <p className='cardPrice'>{item.price} $</p>
                                </Link>
                            </li>

                        ))

                    ) : <h2>Loading...</h2>
                }
            </ul>
        </div>
    )
}

export default Shows