import { useTranslation } from 'react-i18next'
import { Link } from "react-router-dom";
import { useContext } from "react";
import { AuthContext } from "../../context/authContext";
import Logo from '../../assets/logo.svg'
import PorfileMan from '../../assets/porfileMan.png'
import '../../index.css'
import './header.css'

export function Header() {

    const { token } = useContext(AuthContext)

    const { t, i18n } = useTranslation()

    const changeLang = (e) => {
        i18n.changeLanguage(e.target.value)
        const lngBtn = (e.target.value);
        localStorage.setItem("lng", lngBtn)
    }



    return (
        <div className="header container" style={{ display: 'flex', gap: '10px' }}>
            <Link to="/">
                <img style={{ marginRight: '14px', paddingBottom: '10px' }} src={Logo} alt="kivi logo" width={67} height={67}></img>
            </Link>

            <p className="headerText" style={{ maxWidth: '152px', marginRight: '79px' }}>{t('headerText')}</p>

            <div style={{ width: '425px', display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginRight: '76px' }}>
                <Link style={{ textDecoration: 'none' }} className="headerLink" to="/products">
                    <p className="headerLinkText">{t('ads')}</p>
                </Link>

                <Link style={{ textDecoration: 'none' }} className="headerLink" to="/shops">
                    <p className="headerLinkText">{t('shops')}</p>
                </Link>

                <Link style={{ textDecoration: 'none' }} className="headerLink" to="/business">
                    <p className="headerLinkText">{t('forBusiness')}</p>
                </Link>

                <Link style={{ textDecoration: 'none' }} className="headerLink" to="/helps">
                    <p className="headerLinkText">{t('help')}</p>
                </Link>
            </div>

            <div style={{ width: '79px', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}
                defaultValue='ru'
                onClick={changeLang}>
                <option style={{ cursor: 'pointer' }} value='ru' className="headerLang"></option>
                <span className="blackLineReg"></span>
                <option style={{ cursor: 'pointer' }} value='uz' className="headerLang">O'z</option>
            </div>


            {
                token ? (
                    <Link className="profRegLink" to='/profile'>
                        <div style={{ width: '122px', display: 'flex', alignItems: 'center', justifyContent: 'space-between', height: '71px', marginLeft: '66px' }}>
                            <p style={{ margin: '0' }}>{t('name')}</p>
                            <img src={PorfileMan}></img>
                        </div>
                    </Link>


                ) : (
                    <div style={{ width: '180px', display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginLeft: '66px' }}>
                        <p className="headerReg">{t('add')}</p>

                        <span className="blueLineReg"></span>

                        <Link className="headerRegLink" to="/login">
                            <p className="headerReg">{t('reg')}</p>
                        </Link>
                    </div>
                )
            }



        </div>
    )
}