import Services from '../services/services'
import ServicesItem from '../../components/servicesItem/servicesItem'
import Airplane from '../../assets/airplane.svg'
import Store from '../../assets/store.svg'
import Terminal from '../../assets/terminal.svg'
import Briefcase from '../../assets/briefcase.svg'
import Gifts from '../../assets/gifts.svg'
import Challenge from '../../assets/challenge.svg'
import Maintenance from '../../assets/maintenance.svg'
import Gaming from '../../assets/gaming.svg'
import Man from '../../assets/man.svg'
import '../../index.css'
import { Link, Outlet, Route, Routes } from 'react-router-dom'
import './home.css'
import Shows from '../shows/shows'


function HomePage() {
    return (
        <div>
            <div>
                <div className='container'>
                    <Services>
                        <ServicesItem
                            img={Airplane}
                            title=''
                            id='img1'
                        />

                        <ServicesItem
                            img={Store}
                            title=''
                            id='img2'
                        />

                        <ServicesItem
                            img={Terminal}
                            title=''
                            id='img3'
                        />

                        <ServicesItem
                            img={Briefcase}
                            title=''
                            id='img4'
                        />


                        <ServicesItem
                            img={Gifts}
                            title=' '
                            id='img5'
                        />

                        <ServicesItem
                            img={Challenge}
                            title='  '
                            id='img6'
                        />

                        <ServicesItem
                            img={Maintenance}
                            title='  '
                            id='img7'
                        />
                        <ServicesItem
                            img={Gaming}
                            title='   '
                            id='img8'
                        />

                        <ServicesItem
                            img={Man}
                            title='   '
                            id='img9'
                        />
                    </Services>
                </div>

                <div>
                    <div className='container homeTopNestedMenuWrapper'>
                        <Link className='homeTopNestedMenu' to='/'> </Link>
                        <Link className='homeTopNestedMenu' to='/best-ads'> </Link>
                    </div>

                    <Outlet />
                </div>

                <div style={{marginTop: '26px'}} className='container homeTopNestedMenuWrapper'>
                    <Link className='homeTopNestedMenu' to='/'></Link>
                    <Link className='homeTopNestedMenu' to='choose'> </Link>
                    <Link className='homeTopNestedMenu' to='less-price'> </Link>
                </div>
            </div>



            <Routes>
                <Route path='/*' element={<Shows />} />
                <Route path='/choose'  className="container" style={{paddingBottom: '20px', paddingTop: '20px'}} element={<h2 className='container'> </h2>} />
                <Route path='/less-price'  className="container" style={{paddingBottom: '20px', paddingTop: '20px'}} element={<h2 className='container'> </h2>} />
            </Routes>
        </div>
    )
}

export default HomePage