import './newAds.css'
import '../../index.css'
import HomeImg1 from '../../assets/homeImg1.png'
import HomeImg2 from '../../assets/homeImg2.png'
import HomeImg3 from '../../assets/homeImg3.png'
import CardItem from '../cardItems/cardItems'
import Card from '../card/card'


function NewAds() {
    return (
        <div className=''>
            <div className='container'>
                <Card>
                    <CardItem/>
                </Card>
            </div>

            <div className='homeNoContainer'>
                <div className='container'>
                    <div className='homeSlider'>
                        <img style={{ marginRight: '29px' }} src={HomeImg1}></img>
                        <img style={{ marginRight: '29px' }} src={HomeImg2}></img>
                        <img src={HomeImg3}></img>
                    </div>
                </div>
            </div>

            
        </div>
    )
}

export default NewAds


